/* src/styles/contact.css */

@media (min-width:480px) {
  .contact-section{
    display: none;
  }

  .btn-clientes-mobile{
    display: none;
  }

  .logos-clientes{
    display: none;
  }
}

@media (max-width:480px) {

  .contact-section {
    background: linear-gradient(to bottom, #1D1D1B 53%, #f9b734 123%);
    padding: 8vw 5vw;
    color: #fff;
  }

  .contact-section h2 {
    position: relative;
    font-size: 4.7vw;
    left: -22%;
    text-align: center;
    margin-bottom: 6vw;
    font-family: "Montserrat", sans-serif;
  }

  .contact-section .input-group {
    margin-bottom: 5vw;
    display: flex;
    flex-direction: column;
  }

  .contact-section .input-label {
    font-size: 4vw;
    color: #f9b734;
    font-family: "Montserrat", sans-serif;
    margin-bottom: 1.5vw;
  }

  .contact-section .input-field,
  .contact-section .textarea-field {
    background-color: #333;
    border: none;
    border-radius: 1vw;
    color: #fff;
    outline: none;
  }

  .contact-section .input-field {
    padding: 3vw;
    font-size: 4vw;
  }

  .contact-section .textarea-field {
    padding: 4vw 3vw;
    font-size: 4vw;
    min-height: 28vw;
    resize: none;
  }

  .contact-section .btn-submit {
    margin-top: 6vw;
    background-color: #000;
    color: #fff;
    border: none;
    border-radius: 2vw;
    padding: 4vw 6vw;
    font-size: 4.5vw;
    font-weight: 700;
    width: 100%;
    max-width: 60vw;
    display: block;
    margin: 0 auto;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }

  .contact-section .btn-submit:hover {
    background-color: #222;
  }
}